.layout {
  max-width: 1228px;
  min-width: 375px;
  padding: 0 18px;
  margin: 0 auto;

  @media (min-width: 1200px) {
    padding: 0;
  }

  &__header {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
  }
  &__heading {
    margin-right: 54px;
  }

  .buttons {
    display: flex;
    align-items: center;

    &__group {
      display: inherit;
      align-items: inherit;
      margin-right: 36px;
      position: relative;

      &--output {
        display: none;

        @media (min-width: 992px) {
          display: block;
        }
      }

      &:after {
        content: '';
        top: 0;
        right: -18px;
        position: absolute;
        width: 1px;
        height: 100%;
        background-color: var(--lm-gray-light-1);
      }
    }

    .button {
      margin-right: 18px;

      &:last-child {
        margin-right: 0;
      }
    }
  }
}
