.checkbox {
  position: relative;
  display: inline-flex;
  width: 14px;
  height: 14px;
  cursor: pointer;

  &__input {
    position: absolute;
    width: 0;
    height: 0;
    opacity: 0;

    &:checked {
      ~ .checkbox__mark {
        border: var(--lm-green);
        background-color: var(--lm-green);

        .checkbox__icon {
          width: 7px;
          height: 7px;
          transform: scale(1);
        }
      }
    }

    &:focus-visible {
      ~ .checkbox__mark {
        box-shadow: 0 0 0 2px var(--lm-blue);
      }
    }
  }

  &__mark {
    display: inline-flex;
    align-items: center;
    padding-left: 3px;
    width: 14px;
    height: 14px;
    border: 1px solid #acacac;
    border-radius: 4px;
  }

  &__icon {
    width: 7px;
    height: 7px;
    transform: scale(0);
  }

  &--read-only {
    cursor: initial;
  }
}
