.table {
  border-spacing: 0;
  border-collapse: collapse;
  text-align: center;

  &__head {
    .table__row {
      .table__cell {
        border-bottom: 1px solid var(--lm-gray-mid-2);

        padding-top: 18px;
        padding-bottom: 3px;
      }
    }
  }

  &__body {
    .table__row {
      .table__cell {
        border-bottom: 1px solid var(--lm-gray-light-1);
      }
      &:hover {
        .table__cell {
          background-color: var(--lm-gray-light-2);
        }
      }
    }
  }

  &__cell {
    padding: 10px 8px;
    vertical-align: middle;
    min-width: 64px;

    &:first-child {
      padding-left: 0;
    }
    &:last-child {
      padding-right: 0;
    }
  }
}
