.tab-panel {
  margin-top: 18px;
  background-color: var(--lm-white);
  padding: 18px 6px;
  margin-left: -18px;
  margin-right: -18px;

  @media (min-width: 480px) {
    padding: 18px 24px;
  }

  @media (min-width: 768px) {
    padding: 20px 36px;
    margin-top: 24px;
    margin-left: 0;
    margin-right: 0;
  }

  &__inner {
    position: relative;
    overflow-x: auto;
  }
}

.sticky {
  position: sticky;
  left: 0;
  z-index: 2;
  background-color: var(--lm-white);
  text-align: left;
}

.button--heading {
  background: none;
  border: none;
  padding: 0;
  outline: none;
  cursor: pointer;

  &:focus-visible {
    box-shadow: 0 0 0 2px var(--lm-blue);
  }
}

.table { 
  margin: 0 auto;
}
