.tabs {
  display: inline-flex;
  position: relative;
  align-items: center;

  &:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: var(--lm-black);
  }

  &__inner {
    display: flex;
    overflow: auto;

    &::-webkit-scrollbar {
      display: none;
    }
  }
}

.tab {
  position: relative;
  min-width: 90px;
  padding: 14px 12px;
  color: var(--lm-gray-mid-1);
  font-family: var(--font-family-secondary);
  background: none;
  border: none;
  outline: none;

  &:hover {
    color: var(--lm-black);
  }

  &:active {
    color: var(--lm-black);
  }

  &:focus-visible {
    color: var(--lm-black);
    box-shadow: inset 0 0 0 2px var(--lm-blue);
  }

  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 6px;
    background-color: var(--lm-black);
    opacity: 0;
    transition: opacity 0.2s ease-in;
  }

  &--active {
    color: var(--lm-black);
    &:after {
     opacity: 1;
    }
  }
}
