.range {
  --range-height: 8px;
  --range-padding: 2px;
  --range-track-height: 4px;
  display: flex;
  justify-content: center;
  align-items: center;

  &__inner {
    display: block;
    position: relative;
    width: 100%;
    height: var(--range-height);
    padding: var(--range-padding) 0;
    width: 62px;
  }

  &__track {
    position: absolute;
    width: 100%;
    background-color: var(--lm-gray-light-1);
    height: var(--range-track-height);
  }

  &__mark {
    position: absolute;
    top: calc(50% - var(--range-padding) * 2);
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: var(--lm-gray-mid-2);
  }

  &__label {
    font-family: var(--font-family-secondary);
    color: var(--lm-gray-mid-2);
    font-weight: 400;
    font-size: 10px;
    line-height: 1.1;
    margin-right: 10px;

    &:last-child {
      margin-right: 0;
      margin-left: 10px;
    }
  }

  
}
