.tag {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-family: var(--font-family-secondary);
  font-weight: 700;
  font-size: 10px;
  line-height: 1.15;
  min-width: 50px;
  border-radius: 4px;
  height: 20px;
  color: var(--lm-white);
  padding: 3px 6px 4px;


  &--veryGood {
    background-color: var(--grade-green-dark);
  }

  &--good {
    background-color: var(--grade-green);
  }

  &--neutral {
    background-color: var(--grade-yellow);
  }

  &--bad {
    background-color: var(--grade-red);
  }

  &--veryBad {
    background-color: var(--grade-red-dark);
  }

  &--empty {
    background-color: var(--lm-gray-light-1);
    color: var(--lm-gray-mid-2);
  }
}