.type-primary {
  font-family: var(--font-family-primary);
  line-height: 1.2;
}
.type-secondary {
  font-family: var(--font-family-secondary);
  line-height: 1.15;
}

.type--table

.size-extraSmall {
  font-size: 10px;
}
.size-small {
  font-size: 12px;
}
.size-large {
  font-size: 24px;
}

.weight-regular {
  font-weight: 400;
}
.weight-bold {
  font-weight: 700;
}
.weight-heavy {
  font-weight: 900;
}

.color-black {
  color: var(--lm-black)
}
.color-white {
  color: var(--lm-white)
}
.color-red {
  color: var(--lm-red)
}
.color-green {
  color: var(--lm-green)
}
.color-blue {
  color: var(--lm-blue)
}
.color-gray {
  color: var(--lm-gray-mid-2)
}
.color-darkGray {
  color: var(--lm-gray-mid-1)
}